.body {
  font-family: "Jost", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  color: #333136;
  background-color: #FFFFFF;
  margin: 0;
  padding: 0;
}
@media screen and (max-width: 700px) {
  .body {
    padding: 0 "20px";
  }
}

h1, h2, h3 {
  margin: 0;
  padding: 0;
  font-weight: 500;
}

h1 {
  font-size: 48px;
  line-height: 48px;
}

h2 {
  font-size: 40px;
  line-height: 48px;
  letter-spacing: 2px;
}

h3 {
  font-size: 20px;
  line-height: 26px;
  letter-spacing: 5px;
  text-transform: uppercase;
}

section {
  margin-bottom: "100";
}

@media screen and (min-width: 700px) {
  .wrapper {
    margin: 0 "40px";
  }
}
@media screen and (min-width: 1200px) {
  .wrapper {
    width: 1100px;
    margin: 0 auto;
  }
}